<template>
  <div>
    <users-select @item-selected="(val) => findUser(1, val)" />

    <b-card>
      <form
        @submit.prevent="
          findUser(null, searchColumnSelected, searchColumnValue)
        "
      >
        <b-row class="mt-4 d-flex align-items-end">
          <b-col md="3">
            <b-form-group horizontal label="Search By" class="mb-0">
              <b-form-select
                :options="searchColumnOptions"
                v-model="searchColumnSelected"
              />
            </b-form-group>
          </b-col>

          <b-col md="7">
            <b-form-group horizontal label="Value" class="mb-0">
              <b-input v-model="searchColumnValue" />
            </b-form-group>
          </b-col>

          <b-col md="2" sm="2">
            <button
              type="submit"
              class="btn btn-success btn-block"
              :disabled="!!searchColumnValue ? false : true"
            >
              Search
              <i class="fa fa-search"></i>
            </button>
          </b-col>
        </b-row>
      </form>

      <hr />

      <b-row class="mt-3 print__action">
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Per page" class="mb-0">
            <b-form-select
              :options="pageOptions"
              v-model="perPage"
              @change="(val) => setPerPage(val)"
            />
          </b-form-group>
        </b-col>

        <b-col md="4" sm="4" class="my-2">
          <b-form-group horizontal label="Sort" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- none --</option>
              </b-form-select>

              <b-form-select
                :disabled="!sortBy"
                v-model="sortDesc"
                slot="append"
              >
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="2" sm="2" class="my-2">
          <label for>Actions</label>
          <button
            @click="printTable(true, 'none')"
            class="btn btn-secondary btn-block text-black"
          >
            Print
            <i class="fa fa-print"></i>
          </button>
        </b-col>
      </b-row>

      <b-table
        v-if="!loading"
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        responsive="sm"
        :items="items"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        @filtered="onFiltered"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        class="mt-3"
      >
        <template v-slot:cell(vaultname)="data">
          <span class="badge badge-primary">{{
            data.item.user ? data.item.user.username : undefined
          }}</span>
        </template>

        <template v-slot:cell(actions)="data">
          <router-link
            :to="'edit/' + data.item.id"
            class="btn btn-sm btn-dark ml-1"
          >
            Edit
            <i class="fa fa-pencil" />
          </router-link>
        </template>
      </b-table>

      <nav>
        <paginate
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </b-card>
  </div>
</template>

<script>
import { serverBus } from "@/main";
import UsersSelect from "@/components/UsersSelect";

import _ from "lodash";
import Paginate from "vuejs-paginate";

export default {
  name: "vaults-list",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true
    },
    striped: {
      type: Boolean,
      default: true
    },
    bordered: {
      type: Boolean,
      default: true
    },
    small: {
      type: Boolean,
      default: true
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    sortOptions() {
      return this.fields
        .filter((f) => f.sortable)
        .map((f) => {
          return { text: f.label, value: f.key };
        });
    }
  },
  components: {
    Paginate,
    UsersSelect
  },
  data() {
    return {
      items: [
        {
          user: {}
        }
      ],
      fields: [
        { key: "id", sortable: true, label: "ID", tdClass: "first-id__class" },
        {
          key: "vaultname",
          sortable: true,
          label: "Username"
        },
        {
          key: "amount",
          sortable: true,
          label: "Amount",
          class: "text-center"
        },
        {
          key: "total_rewards",
          sortable: true,
          label: "Right vaults",
          class: "text-center"
        },
        {
          key: "total_withdrawals",
          sortable: true,
          label: "Diff",
          class: "text-center"
        },
        {
          key: "actions",
          label: "actions",
          class: "text-center"
        }
      ],
      totalRows: 0,
      loaders: {},
      show: true,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100],
      totalItems: 0,
      currentPage: 1,
      pageCount: 1,
      perPage: 25,
      options: [],
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      searchColumnOptions: [
        "email",
        "username",
        "name",
        "last_name",
        "country",
        "city",
        "address"
      ],
      searchColumnSelected: "email",
      searchColumnValue: ""
    };
  },
  created() {
    this.fetchVaults();
    window.onafterprint = () => {
      this.printTable(false, "");
    };
  },
  watch: {
    "$route.query.status"() {
      this.fetchVaults();
    }
  },
  methods: {
    setPerPage(val) {
      this.perPage = val;
      this.fetchVaults();
    },
    printTable(print, myClass) {
      var cols = document.getElementsByClassName("print__action");
      Array.prototype.forEach.call(cols, function(el) {
        el.style.display = myClass;
      });

      if (print) window.print();
    },
    nextPageCall(pageNum) {
      this.fetchVaults(pageNum);
    },
    openDetailModal(item) {
      serverBus.$emit("detailPointModal", item);
    },
    findUser(username, column = null, pattern = null) {
      this.loading = true;
      let url = `1?username=${username}`;
      if (column && pattern) {
        url = `search?column=${column}&pattern=${pattern}`;
      }
      this.$http(`/api/v1/admin/vaults/${url}`, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then((response) => {
          this.items = response.data;
          this.totalItems = response.data.length;
          this.perPage = response.data.length;
          this.pageCount = 1;
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    fetchVaults(pageNo = 1, username = "") {
      this.loading = true;
      username = username && "&username=" + username;
      this.$http(
        "/api/v1/admin/vaults?per_page=" +
          this.perPage +
          "&page=" +
          pageNo +
          username,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        }
      )
        .then((response) => {
          this.items = response.data;
          this.totalItems = parseInt(response.headers.total);
          this.perPage = parseInt(response.headers["per-page"]);
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );

          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    }
  }
};
</script>
